@import url(https://fonts.googleapis.com/css?family=DM+Sans:400,500|Jost:400,500,600&display=swap);
:root {
  --bg_color_light: #ebe6e7;
  --bg_color_dark: #181a19;
  --text_light: #ffffff;
  --text_dark: #000;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/static/media/dark_bg.736e378b.jpg);
  background-repeat: repeat;
  background-size: cover;
  color: #ffffff;
  color: var(--text_light);
  font-family: 'iCiel Gotham', sans-serif, 'Franklin Gothic Medium',
    'Arial Narrow', Arial, sans-serif;
}

.App {
  text-align: center;
}
:root {
  --bg_color_light: #ebe6e7;
  --bg_color_dark: #181a19;
  --text_light: #ffffff;
  --text_dark: #000;
  --cream: hsl(348, 11%, 91%);
  --insta: hsl(348, 35%, 74%);
  --facebook: #6bacd8cc;
  --green: rgba(99, 250, 39, 0.966);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.containerInput {
  top: 50%;
  left: 50%;
  height: 500px;
  width: 1000px;
  position: absolute;
  transform: translate(-50%, -50%);
  background: #ffffff;
  background: var(--text_light);
}

.containerInput {
  box-shadow: 0px 30px 40px black;
  display: flex;
  border-radius: 10px;
}

.image {
  flex: 50% 1;
  background: linear-gradient(45deg, #fe6b8b 30%, #ff8e53 90%);
  background-size: cover;
  text-align: center;
  color: white;
}

.image h1 {
  margin-top: 50%;
  letter-spacing: 2px;
}

.welcome {
  color: #000;
  color: var(--text_dark);
}

.content {
  flex: 50% 1;
  background-color: #181a19;
  background-color: var(--bg_color_dark);
  text-align: center;
}

.content h1 {
  padding-top: 10px;
  padding: 20px;
  font-size: 30px;
  color: #ffffff;
  color: var(--text_light);
}

.content h3 {
  padding-top: 15px;
  font-size: 20px;
  color: #ffffff;
  color: var(--text_light);
}

#txt {
  margin: 8px;
  padding: 5px;
  border: none;
  background-color: rgba(156, 77, 156, 0.3);
  border-radius: 10px;
  font-weight: bold;
  font-size: small;
  color: var(--text_white);
}

label {
  font-weight: bold;
  font-size: small;
}

#txt:focus {
  outline: none;
}

.fp {
  text-decoration: none;
  font-weight: bold;
  font-size: small;
  transition: 0.3s;
  color: var(--blue);
}

.fp:hover {
  color: rgba(99, 250, 39, 0.966);
  color: var(--green);
}

.form-group {
  color: #fff;
}

.form-group-signup {
  margin-top: 5px;
}

button {
  padding: 10px 40px;
  margin-top: 10px;
  border: none;
  background: linear-gradient(45deg, #fe6b8b 30%, #ff8e53 90%);
  border-radius: 5px;
}

hr {
  margin-top: 20px;
}

button a {
  text-decoration: none;
  color: white;
}

.login {
  margin-top: 15px;
  margin-bottom: 10px;
  color: #ffffff;
  color: var(--text_light);
  border-radius: 10px;
  font-weight: bold;
  font-size: small;
}
.links {
  display: flex;
  justify-content: space-around;
  margin: 20px;
  cursor: pointer;
  color: #000;
  color: var(--text_dark);
}

.links-signup {
  margin: 15px;
}

.facebook,
.instagram,
.google {
  height: 45px;
  line-height: 45px;
}
.facebook {
  width: 30%;
  background: #6bacd8cc;
  background: var(--facebook);
  border: 1px #6bacd8cc;
  border: 1px var(--facebook);
}
.instagram {
  right: 0;
  width: 30%;
  background: hsl(348, 35%, 74%);
  background: var(--insta);
  border: 1px solid hsl(348, 35%, 74%);
  border: 1px solid var(--insta);
}
.facebook:hover {
  background: #3e61a8;
}
.instagram:hover {
  background: hsl(348, 11%, 91%);
  background: var(--cream);
}

.google {
  right: 0;
  width: 30%;
  background: rgb(212, 201, 201);
  border: 1px solid rgb(212, 201, 201);
}
.google:hover {
  background: lightgray;
  border: 1px solid lightgray;
}

.links i {
  font-size: 13px;
  margin: 0 auto;
}
i span {
  margin-left: 8px;
  font-weight: 500;
}
.signup {
  margin: 40px;
  font-size: 13px;
  color: #ffffff;
  color: var(--text_light);
}
.signup a {
  color: var(--blue);
  text-decoration: none;
}
.signup a:hover {
  text-decoration: underline;
}

button a:hover {
  color: #ffffff;
  color: var(--text_light);
}

.signup-signup {
  margin: 10px;
}

@media screen and (max-width: 768px) {
  .image {
    display: none;
  }
  .containerInput {
    width: 100%;
    height: 90%;
  }

  .signup a {
    font-size: 13px;
    color: var(--blue);
    text-decoration: none;
  }

  .login a {
    font-size: 13px;
    color: var(--blue);
    text-decoration: none;
  }

  .signup-signup {
    margin: 15px;
  }
}

.login-error {
  margin: 15px;
  color: crimson;
}

.login-error:hover {
  color: crimson;
}

:root {
  --bg_color_light: #ebe6e7;
  --bg_color_dark: #181a19;
  --text_light: #ffffff;
  --text_dark: #000;
  --cream: rgba(247, 166, 166, 0.8);
  --green: rgba(99, 250, 39, 0.966);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'iCiel Gotham', sans-serif, 'Franklin Gothic Medium',
    'Arial Narrow', Arial, sans-serif;
}
nav {
  display: flex;
  height: 50px;
  background-color: rgba(247, 166, 166, 0.8);
  background-color: var(--cream);
  background: #181a19;
  background: var(--bg_color_dark);
  position: relative;
}

.nav-items {
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  margin-left: auto;
  width: 50%;
  height: 100%;
}

.nav-search {
  margin: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  width: 50%;
  height: 50%;
}

.search-field {
  color: black;
  width: 50%;
}

.logo {
  display: flex;
  margin-top: 5px;
  margin-left: 8px;
  float: left;
}

.logo a {
  margin: auto;
  font-size: 1.2em;
}

a {
  color: white;
  text-decoration: none;
  font-weight: 600;
}

a:hover {
  color: rgba(99, 250, 39, 0.966);
  color: var(--green);
  transition: color 0.4s ease-in-out;
}

.disabled {
  display: none;
}

.nav-item p {
  margin-top: 19px;
  font-size: inherit;
}

@media screen and (max-width: 768px) {

  .logo a {
    margin: 8px;
  }

  .hamburger {
    position: absolute;
    right: 5%;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 3;
  }

  .line {
    display: block;
    height: 3px;
    width: 20px;
    margin: 4px;
    border-radius: 3px;
    background-color: white;
  }

  .nav-items {
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: #ff6961;
    flex-direction: column;
    -webkit-clip-path: circle(1px at 85% -10%);
    clip-path: circle(1px at 85% -10%);
    transition: all 0.8s ease-out;
    pointer-events: none;
    z-index: 2;
  }
  .nav-items.show {
    display: flex;
    -webkit-clip-path: circle(1000px at 85% -20%);
    clip-path: circle(2000px at 100% -20%);
    pointer-events: all;
  }

  a {
    font-size: 25px;
  }

  .nav-search {
    margin: 10px 20px;
  }

  .nav-item {
    opacity: 100%;
  }

  .search-field {
    width: 75%;
  }

  .nav-item:nth-child(1).open {
    transition: all 0.5s ease 0.2s;
    margin-top: 30%;

  }
  .nav-item:nth-child(2).open{
    transition: all 0.5s ease 0.4s;
  }
  .nav-item:nth-child(3).open{
    transition: all 0.5s ease 0.6s;
  }

  .nav-item:nth-child(4).open{
    transition: all 0.5s ease 0.8s;
    margin-bottom: 30%;
    margin-left: 3%;
  }

  .nav-item.fade {
    opacity: 1;
  }
}

