  display: flex;
  transition: 0.3s;
  flex-direction: column;
}

.card[data-state='#about'] .card-main {
  padding-top: 0;
}
.card[data-state='#contact'] {
  height: 430px;
}

.card.is-active .card-header {
  height: 80px;
}
.card.is-active .card-cover {
  height: 90px;
}
.card.is-active .card-avatar {
  transform: none;
  left: 20px;
  width: 50px;
  height: 50px;
  bottom: 10px;
}
.card.is-active .card-fullname,
.card.is-active .card-jobtitle {
  left: 86px;
  transform: none;
}
.card.is-active .card-fullname {
  bottom: 25px;
  font-size: 19px;
}
.card.is-active .card-jobtitle {
  bottom: 10px;
  letter-spacing: 1px;
  font-size: 10px;
}

.card-header {
  color: #181a19;
  color: var(--bg_color_dark);
  position: relative;
  display: flex;
  height: 200px;
  flex-shrink: 0;
  width: 100%;
  transition: 0.3s;
}

.card-contact-center {
  margin-top: 20px;
}

.card-header * {
  transition: 0.3s;
}
.card-cover {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  will-change: top;
  background: linear-gradient(
    45deg,
    rgba(248, 195, 69, 1) 0%,
    rgba(244, 124, 118, 1) 100%
  );
  -webkit-filter: blur(1px);
          filter: blur(1px);
  transition: 0.5s;
}
.card-avatar {
  width: 100px;
  height: 100px;
  box-shadow: 0 8px 8px rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  object-position: center;
  object-fit: cover;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(-64px);
}
.card-fullname {
  position: absolute;
  bottom: 8%;
  font-size: 22px;
  font-weight: 700;
  text-align: center;
  white-space: nowrap;
  transform: translateY(-10px) translateX(-50%);
  left: 50%;
}
.card-jobtitle {
  position: absolute;
  bottom: 0;
  font-size: 11px;
  white-space: nowrap;
  font-weight: 500;
  opacity: 0.7;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin: 0;
  left: 50%;
  transform: translateX(-50%) translateY(-7px);
}
.card-main {
  position: relative;
  flex: 1 1;
  display: flex;
  padding-top: 10px;
  flex-direction: column;
}
.card-subtitle {
  font-weight: 700;
  font-size: 13px;
  color: #000;
}
.card-content {
  display: flex;
  flex-direction: column;
  padding: 20px;
}
.card-desc {
  position: absolute;
  line-height: 1.6;
  color: #636b6f;
  font-size: 14px;
  margin: 0;
  text-align: left;
  margin-top: 45px;
  font-weight: 400;
  font-family: 'DM Sans', sans-serif;
  word-wrap: break-word;
  padding-right: 5px;
}
.card-social {
  margin-top: 50px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  padding: 0 20px;
}
.card-social svg {
  fill: #a5b5ce;
  width: 16px;
